import styled from '@emotion/styled';
import React from 'react';
import { ReactComponent as HowMany } from '../images/step/title_how_many.svg';

const Invite = ({ friends }) => {
  const Box = styled.div`
    margin: ${(props) => props.margin};
  `;

  const FriendBox = styled.div`
    margin: ${(props) => props.margin};
    background-color: #f5f5f5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 6vw;
    border-radius: 5vw;
    height: 21vw;
    h5 {
      margin: 0;
      font-weight: 700;
      font-size: 4vw;
    }
    p {
      margin: 0;
      font-size: 9vw;
      font-weight: 700;
      color: #fe5000;
    }
  `;

  const Line = styled.div`
    margin: ${(props) => props.margin};
    height: 1vw;
    background-color: #f5f5f5;
  `;

  return (
    <>
      <Box margin={`0 0 13vw 0`}>
        <HowMany width="53vw" />
      </Box>
      <FriendBox margin={`0 4vw 0 4vw`}>
        <h5>가입한 친구</h5>
        <p>{friends}명</p>
      </FriendBox>
      <Line margin={`7vw 0 7vw 0`}></Line>
    </>
  );
};

export default Invite;
