import styled from '@emotion/styled';
import React from 'react';

const Modal = ({ onClose, type }) => {
  const Container = styled.div`
    width: 100%;
    height: 100%;
    position: fixed;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.86);
    z-index: 40;
    display: flex;
    justify-content: center;
    align-items: center;
  `;
  const Contents = styled.div`
    margin: 0 auto;
    background: #ffffff;
    border-radius: 4vw;
    /* padding: 30px; */
    height: 38vw;
    width: 100%;
    margin: 6vw;
  `;
  const CopyDetail = styled.div`
    padding: 6vw;
    p {
      font-weight: 700;
      font-size: 5vw;
    }
    button {
      cursor: pointer;
      width: 100%;
      height: 11vw;
      font-size: 4vw;
      font-weight: 700;
      color: #ffffff;
      text-align: center;
      line-height: 100%;
      border-radius: 2vw;
      background-color: #fe5000;
      border: none;
      &:hover {
        background-color: #cc4400;
      }
      &:active {
        background-color: #cc4400;
      }
    }
  `;

  return (
    <>
      <Container onClick={() => onClose()}>
        <Contents onClick={(e) => e.stopPropagation()}>
          {type === 'copy' && (
            <CopyDetail>
              <p>링크가 복사되었어요!</p>
              <button onClick={() => onClose()}>닫기</button>
            </CopyDetail>
          )}
        </Contents>
      </Container>
    </>
  );
};

export default Modal;
