import styled from '@emotion/styled';
import React, { useEffect, useState } from 'react';
import { ReactComponent as Checked } from '../images/bottomsheet/checkbox_checked.svg';
import { ReactComponent as UnChecked } from '../images/bottomsheet/checkbox_unchecked.svg';
import Btn from './Btn';

function CheckBox({ agreeItems, setAgreeItems, setReadMe, onClick }) {
  const [mustAgree, setMustAgree] = useState(false);

  useEffect(() => {
    if (
      agreeItems[0]?.isChecked === true &&
      agreeItems[1]?.isChecked === true
    ) {
      setMustAgree(true);
    } else {
      setMustAgree(false);
    }
  }, [agreeItems]);

  const handleChange = (e) => {
    const { name, checked } = e.target;
    if (name === 'allSelect') {
      const allCheck = agreeItems.map((agreeItem) => {
        return { ...agreeItem, isChecked: checked };
      });
      setAgreeItems(allCheck);
    } else {
      const check = agreeItems.map((agreeItem) =>
        agreeItem.name === name
          ? { ...agreeItem, isChecked: checked }
          : agreeItem
      );
      setAgreeItems(check);
    }
  };

  const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 0 4vw;
  `;
  const Checkbox = styled.div`
    display: flex;
    justify-content: space-between;
    padding: 0 4vw;

    p {
      font-size: 4vw;
      color: #757575;
      font-weight: 700;
      cursor: pointer;
    }
  `;

  const Input = styled.input`
    -webkit-appearance: none;
    -webkit-border-radius: 0;
    &[type='checkbox'] {
      display: none;
    }
  `;

  const AllLabel = styled.label`
    display: flex;
    align-items: center;
    background-color: #ffebe0;
    border-radius: 2vw;
    gap: 3vw;
    padding: 0 4vw;
    height: 13vw;
    cursor: pointer;

    h6 {
      font-size: 4vw;
      font-weight: 700;
      color: #3d3d3d;
    }
  `;

  const Label = styled.label`
    display: flex;
    height: 13vw;
    align-items: center;
    cursor: pointer;
    margin: 0;
    padding: 0;

    div {
      display: flex;
      gap: 3vw;
    }
    h6 {
      margin: 0;
      font-size: 4vw;
      font-weight: 400;
      color: #757575;
    }
  `;
  return (
    <Container>
      <AllLabel>
        <div>
          {agreeItems[0]?.isChecked && agreeItems[1]?.isChecked ? (
            <Checked width="5vw" />
          ) : (
            <UnChecked width="5vw" />
          )}
        </div>
        <h6>모두 동의합니다.</h6>
        <Input
          type="checkbox"
          name="allSelect"
          checked={
            !agreeItems.some((agreeItem) => agreeItem?.isChecked !== true)
          }
          onChange={handleChange}
        />
      </AllLabel>
      {agreeItems.map((agreeItem, index) => (
        <Checkbox key={agreeItem.id}>
          <Label>
            <div>
              <div>
                {agreeItems[agreeItem.id]?.isChecked === true ? (
                  <Checked width="5vw" />
                ) : (
                  <UnChecked width="5vw" />
                )}
              </div>
              <Input
                type="checkbox"
                name={agreeItem.name}
                checked={agreeItem?.isChecked || false}
                onChange={handleChange}
              />
              <h6>{agreeItem.name}</h6>
            </div>
          </Label>
          <p
            onClick={(e) => {
              console.log('onClcik');
              setReadMe(agreeItem.id);
            }}
          >
            보기
          </p>
        </Checkbox>
      ))}
      {agreeItems[0]?.isChecked ? (
        <Btn fixedBtn={false} disabledBtn={false} onClick={onClick}>
          동의하고 친구 초대하기
        </Btn>
      ) : (
        <Btn fixedBtn={false} disabledBtn={true}>
          동의하고 친구 초대하기
        </Btn>
      )}
    </Container>
  );
}

export default CheckBox;
