import styled from '@emotion/styled';
import { useEffect, useState } from 'react';
import BottomSheet from './components/BottomSheet';
import FriendLottie from './components/FriendLottie';
import Invite from './components/Invite';
import Btn from './components/Btn';
import Step from './components/Step';
import initialize from './components/Kakao/initialize';

function App() {
  const [friends, setFriends] = useState(4);
  const [agreeModal, setAgreeModal] = useState(false);
  const [agreeBottomSheet, setAgreeBottomSheet] = useState(false);
  const [shareBottomSheet, setShareBottomSheet] = useState(false);

  //카카오 공유하기 초기화작업
  useEffect(() => {
    initialize();
  }, []);

  //스크롤 방지
  useEffect(() => {
    if ((!agreeModal && !agreeBottomSheet) || !shareBottomSheet) {
      document.body.style.overflow = 'auto';
    }
    if (agreeModal || agreeBottomSheet || shareBottomSheet) {
      document.body.style.overflow = 'hidden';
    }
  }, [agreeModal, agreeBottomSheet, shareBottomSheet]);
  const Container = styled.div`
    margin-bottom: 21vw;
  `;
  return (
    <div className="App">
      {agreeBottomSheet === true ? (
        <BottomSheet
          onClose={() => setAgreeBottomSheet(false)}
          type="agree"
          onClick={() => {
            setShareBottomSheet(true);
            setAgreeBottomSheet(false);
          }}
        />
      ) : null}
      {shareBottomSheet === true ? (
        <BottomSheet type="share" onClose={() => setShareBottomSheet(false)} />
      ) : null}
      <FriendLottie />
      <Step />
      <Invite friends={friends} />

      <Btn
        fixedBtn={true}
        disabledBtn={false}
        onClick={() => setAgreeBottomSheet(true)}
      >
        친구 초대하고 무제한 주식받기
      </Btn>
      {/* <Btn disabledBtn={true} fixedBtn={true}>이벤트가 종료되었어요</Btn> */}

      <Container />
    </div>
  );
}

export default App;
