import React from 'react';
import { ReactComponent as HowTo } from '../images/step/title_how_to.svg';
import { ReactComponent as ImgStep1 } from '../images/step/img_step1.svg';
import { ReactComponent as TitleStep1 } from '../images/step/title_step1.svg';
import { ReactComponent as ImgStep2 } from '../images/step/img_step2.svg';
import { ReactComponent as TitleStep2 } from '../images/step/title_step2.svg';
import { ReactComponent as ImgStep3 } from '../images/step/img_step3.svg';
import { ReactComponent as TitleStep3 } from '../images/step/title_step3.svg';
import { ReactComponent as TitleStep32 } from '../images/step/img_step3-2.svg';
import styled from '@emotion/styled';
const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const Box = styled.div`
  margin: ${(props) => props.margin};
`;

const H5 = styled.h5`
  margin: ${(props) => props.margin};
  font-size: 4vw;
  line-height: 6vw;
  font-weight: 500;
  b {
    font-weight: 700;
  }
`;

const ReadBox = styled.div`
  margin: ${(props) => props.margin};
  border-top: 0.5px solid #757575;
  border-bottom: 0.5px solid #757575;
  text-align: left;
  padding: 5vw 0 6vw 0;
  color: #757575;
  h6 {
    font-weight: 700;
    font-size: 4vw;
    margin: 0 0 2vw 6vw;
    line-height: 6vw;
  }
  div {
    display: flex;
    align-items: start;
    span {
      margin: 0 2vw 0 2vw;
      font-size: 6vw;
      position: relative;
      bottom: 1vw;
    }
    p {
      font-size: 4vw;
      font-weight: 400;
      margin: 0;
      line-height: 6vw;
    }
  }
`;

const Step = () => {
  return (
    <Container>
      <Box margin={`11vw 0 0 0`}>
        <HowTo width="32vw" />
      </Box>
      <Box margin={`11vw 0 0 0`}>
        <ImgStep1 width="91vw" />
      </Box>
      <Box margin={`9vw 0 0 0`}>
        <TitleStep1 width="21vw" />
      </Box>
      <H5 margin={`3vw 0 0 0`}>
        커피하우스에서 <b>증권계좌를 개설해주세요.</b>
      </H5>
      <Box margin={`16vw 0 0 0`}>
        <ImgStep2 width="91vw" />
      </Box>
      <Box margin={`3vw 0 0 0`}>
        <TitleStep2 width="21vw" />
      </Box>
      <H5 margin={`3vw 0 0 0`}>
        주식을 받을 증권사의 계좌를 선택하고
        <br />
        <b>초대링크를 공유해주세요.</b>
      </H5>
      <Box margin={`15vw 0 0 0`}>
        <ImgStep3 width="91vw" />
      </Box>
      <Box margin={`6vw 0 0 0`}>
        <TitleStep3 width="21vw" />
      </Box>
      <Box margin={`7vw 0 0 0`}>
        <TitleStep32 width="91vw" />
      </Box>
      <ReadBox margin={`10vw 5vw 13vw 5vw`}>
        <h6>꼭 읽어주세요!</h6>
        <div>
          <span>·</span>
          <p>
            초대자는 초대링크 발급 시 선택했던 증권사 계좌로 주식을 받을 수
            있어요.
          </p>
        </div>
        <div>
          <span>·</span>
          <p>
            친구는 가입 후 원하는 증권사 계좌를 개설하고 해당 계좌로 주식을 받을
            수 있어요.
          </p>
        </div>
        <div>
          <span>·</span>
          <p>주식은 당첨일로부터 20영업일 이내에 계좌로 지급돼요.</p>
        </div>
        <div>
          <span>·</span>
          <p>본 이벤트는 준비된 경품 수량 소진 시 마감돼요.</p>
        </div>
      </ReadBox>
    </Container>
  );
};

export default Step;
