import styled from '@emotion/styled';
import React from 'react';

const Btn = ({ onClick, disabledBtn, children, fixedBtn }) => {
  const Container = styled.div`
    position: ${(props) =>
      props.children.props.fixedBtn ? 'fixed' : 'static'};
    bottom: 0;
    left: 0;
    right: 0;
    padding: 4vw;
    background-color: #ffffff;
  `;
  const Btn = styled.button`
    cursor: pointer;
    width: 100%;
    height: 13vw;
    font-size: 4vw;
    font-weight: 700;
    color: #ffffff;
    text-align: center;
    line-height: 100%;
    border-radius: 2vw;
    background-color: #fe5000;
    border: none;
    &:hover {
      background-color: #cc4400;
    }
    &:active {
      background-color: #cc4400;
    }
  `;
  const DisabledBtn = styled.button`
    width: 100%;
    height: 13vw;
    font-size: 4vw;
    font-weight: 700;
    color: #a1a1a1;
    text-align: center;
    line-height: 100%;
    border-radius: 2vw;
    background-color: #f5f5f5;
    border: none;
  `;
  return (
    <Container>
      {disabledBtn ? (
        <DisabledBtn fixedBtn={fixedBtn} disabled>
          {children}
        </DisabledBtn>
      ) : (
        <Btn fixedBtn={fixedBtn} onClick={onClick}>
          {children}
        </Btn>
      )}
    </Container>
  );
};

export default Btn;
