import styled from '@emotion/styled';
import React from 'react';

function KakaoBtn({ active }) {
  const onKakaoClick = () => {
    window.Kakao.Link.sendCustom({
      templateId: 83350, // 카카오 디벨롭 사이트에서 만든 템플릿 아이디 넣어주세요.
    });
  };

  const Button = styled.button`
    cursor: pointer;
    width: 100%;
    height: 13vw;
    font-size: 4vw;
    font-weight: 400;
    color: #ffffff;
    text-align: center;
    line-height: 100%;
    border-radius: 2vw;
    background-color: #fe5000;
    border: none;
    &:hover {
      background-color: #cc4400;
    }
    &:active {
      background-color: #cc4400;
    }
  `;

  const DisabledBtn = styled.button`
    width: 100%;
    height: 13vw;
    font-size: 4vw;
    font-weight: 400;
    color: #ffffff;
    text-align: center;
    line-height: 100%;
    border-radius: 2vw;
    background-color: #ffcbb3;
    border: none;
  `;
  return (
    <>
      {active ? (
        <Button onClick={onKakaoClick}>카카오톡 공유</Button>
      ) : (
        <DisabledBtn disabled>카카오톡 공유</DisabledBtn>
      )}
    </>
  );
}

export default KakaoBtn;
