import styled from '@emotion/styled';
import React, { useEffect, useState } from 'react';
import CheckBox from './CheckBox';
import { ReactComponent as CloseBtn } from '../images/bottomsheet/close_btn.svg';
import CopyBtn from './CopyBtn';
import Modal from './Modal';
import { ReactComponent as Namu } from '../images/account/icon_namu.svg';
import { ReactComponent as Kb } from '../images/account/icon_kb.svg';
import { ReactComponent as Sk } from '../images/account/icon_sk.svg';
import { ReactComponent as Shinhan } from '../images/account/icon_shinhan.svg';
import { ReactComponent as Next } from '../images/account/btn_next.svg';
import KakaoBtn from './Kakao/KakaoBtn';

const BottomSheet = ({ onClose, type, onClick }) => {
  const Container = styled.div`
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(0, 0, 0, 0.86);
    z-index: ${(props) => props.z};
    bottom: 0;
  `;
  const Contents = styled.div`
    background: #ffffff;
    width: 100%;
    height: ${(props) => props.height || 'auto'};
    margin-top: 13vw;
    bottom: 0;
    border-radius: 15px 15px 0px 0px;
    position: absolute;
  `;
  const CloseBox = styled.div`
    display: flex;
    justify-content: end;
    padding: 3vw 4vw 0 0;
    cursor: pointer;
  `;
  const [copyModal, setCopyModal] = useState(false);

  return (
    <>
      {type === 'personalAgree' && (
        <>
          <Container z={20} onClick={() => onClose(false)}>
            <Contents
              onClick={(e) => e.stopPropagation()}
              height={`calc(100vh - 13vw)`}
            >
              <CloseBox>
                <CloseBtn width="6vw" onClick={() => onClose(false)} />
              </CloseBox>
              <PersonalAgree />
            </Contents>
          </Container>
        </>
      )}
      {type === 'marketingAgree' && (
        <>
          <Container z={20} onClick={() => onClose(false)}>
            <Contents
              onClick={(e) => e.stopPropagation()}
              height={`calc(100% - 13vw)`}
            >
              <CloseBox>
                <CloseBtn width="6vw" onClick={() => onClose(false)} />
              </CloseBox>
              <MarketingAgree />
            </Contents>
          </Container>
        </>
      )}
      {type === 'share' && (
        <>
          {copyModal && (
            <Modal type="copy" onClose={() => setCopyModal(false)} />
          )}
          <Container z={30} onClick={() => onClose(false)}>
            <Contents onClick={(e) => e.stopPropagation()}>
              <ShareDetail setCopyModal={setCopyModal} />
            </Contents>
          </Container>
        </>
      )}
      {type === 'agree' && (
        <>
          <Container z={10} onClick={() => onClose(false)}>
            <Contents onClick={(e) => e.stopPropagation()}>
              <AgreeDetail onClick={onClick} />
            </Contents>
          </Container>
        </>
      )}
    </>
  );
};

export default BottomSheet;

const AgreeDetail = ({ onClick }) => {
  const [agreeItems, setAgreeItems] = useState([
    { id: 0, name: '[필수] 개인(신용)정보 수집·이용·제공 동의' },
    {
      id: 1,
      name: '[선택] 마케팅 정보 수신 및 활용 동의',
    },
  ]);

  const H3 = styled.h3`
    font-size: 5vw;
    line-height: 150%;
  `;
  const [readMe, setReadMe] = useState(null);
  return (
    <>
      {readMe === 0 ? (
        <BottomSheet type="personalAgree" onClose={() => setReadMe(null)} />
      ) : null}
      {readMe === 1 ? (
        <BottomSheet type="marketingAgree" onClose={() => setReadMe(null)} />
      ) : null}

      <H3>
        이벤트 경품 수령을 위해 개인정보
        <br /> 수집·이용·제공 동의가 필요합니다.
      </H3>
      <CheckBox
        onClick={onClick}
        agreeItems={agreeItems}
        setAgreeItems={setAgreeItems}
        setReadMe={setReadMe}
      />
    </>
  );
};

const ShareDetail = ({ setCopyModal }) => {
  const [haveAccount, setHaveAccount] = useState(null);
  const [accountList, setAccountList] = useState([
    { id: 0, name: '나무증권', status: 1, checked: true },
    {
      id: 1,
      name: '신한투자증권',
      status: 0,
    },
    {
      id: 2,
      name: 'KB증권',
      status: 0,
    },
    {
      id: 3,
      name: 'SK증권',
      status: 2,
    },
  ]);
  useEffect(() => {
    const haveAccountArray = accountList.filter((item) => item.status === 0);
    setHaveAccount(haveAccountArray.length > 0);
    console.log('증권사 계좌 소유 : ', haveAccountArray.length);
  }, [accountList]);
  console.log('증권사 계좌 소유 boolean : ', haveAccount);
  const BtnBox = styled.div`
    display: flex;
    gap: 2vw;
    margin: 2vw;
  `;
  const Title = styled.div`
    display: flex;
    gap: 4vw;
    align-items: center;
    p {
      margin: 0;
      font-size: 4vw;
      font-weight: 700;
    }
  `;
  const AccountBox = styled.div`
    display: flex;
    justify-content: space-between;
    margin: 3vw;
    align-items: center;
    cursor: pointer;
    input {
      &[type='radio'] {
        cursor: pointer;
        accent-color: #fe5000;
        width: 6vw;
        height: 6vw;
      }
    }
    label {
      cursor: pointer;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  `;
  const AccountContainer = styled.div`
    margin: 9vw 0;
  `;
  const Container = styled.div`
    h3 {
      margin: 6vw 0 0 0;
      font-size: 5vw;
      line-height: 150%;
    }
    h4 {
      margin: 2vw 0 0 0;
      font-size: 3.7vw;
      color: #757575;
      font-weight: 400;
    }
  `;

  const CreateBtn = styled.button`
    display: flex;
    align-items: center;
    gap: 2vw;
    cursor: pointer;
    height: 8.5vw;
    font-size: 4vw;
    font-weight: 700;
    padding: 2vw 3vw;
    color: #ffffff;
    text-align: center;
    line-height: 100%;
    border-radius: 2vw;
    background-color: #fe5000;
    border: none;
    &:hover {
      background-color: #cc4400;
    }
    &:active {
      background-color: #cc4400;
    }
  `;

  const handleChange = (e) => {
    const { id, value } = e.target;
    console.log('선택된 증권사 이름 :', value);
    console.log('선택된 증권사 id :', id);
  };

  return (
    <Container>
      {haveAccount ? (
        <h3>
          증권사를 선택하고
          <br /> 초대링크를 공유해주세요
        </h3>
      ) : (
        <h3>
          주식을 받을 <br />
          증권사 계좌가 필요해요
        </h3>
      )}

      <h4>선택한 증권사 계좌로 주식이 지급됩니다</h4>
      <AccountContainer>
        {accountList.map((account) => (
          <div key={account.id}>
            {account.status === 0 && (
              <AccountBox key={account.id}>
                <label htmlFor={account.id} onClick={handleChange}>
                  <Title>
                    {account.id === 0 && <Namu width="10vw" />}
                    {account.id === 1 && <Shinhan width="10vw" />}
                    {account.id === 2 && <Kb width="10vw" />}
                    {account.id === 3 && <Sk width="10vw" />}
                    <p>{account.name}</p>
                  </Title>
                  <input
                    type="radio"
                    name="account"
                    value={account.name}
                    onChange={handleChange}
                    id={account.id}
                    checked={account.checked}
                  />
                </label>
              </AccountBox>
            )}

            {account.status === 1 && (
              <AccountBox key={account.id}>
                <Title>
                  {account.id === 0 && <Namu width="10vw" />}
                  {account.id === 1 && <Shinhan width="10vw" />}
                  {account.id === 2 && <Kb width="10vw" />}
                  {account.id === 3 && <Sk width="10vw" />}
                  <p>{account.name}</p>
                </Title>
                <CreateBtn
                  onClick={() => {
                    console.log('개좌개설 증권사 이름 :', account.name);
                    console.log('개좌개설 증권사 id :', account.id);
                  }}
                >
                  <div>개좌개설</div>
                  <div>
                    <Next width="2vw" />
                  </div>
                </CreateBtn>
              </AccountBox>
            )}
            {account.status === 2 && (
              <AccountBox key={account.id}>
                <Title>
                  {account.id === 0 && <Namu width="10vw" />}
                  {account.id === 1 && <Shinhan width="10vw" />}
                  {account.id === 2 && <Kb width="10vw" />}
                  {account.id === 3 && <Sk width="10vw" />}
                  <p>{account.name}</p>
                </Title>
                <CreateBtn
                  onClick={() => {
                    console.log('개좌개설 증권사 이름 :', account.name);
                    console.log('개좌개설 증권사 id :', account.id);
                  }}
                >
                  <div> 개좌개설 및 연동</div>
                  <div>
                    <Next width="2vw" />
                  </div>
                </CreateBtn>
              </AccountBox>
            )}
          </div>
        ))}
      </AccountContainer>
      {haveAccount ? (
        <BtnBox>
          <CopyBtn active={true} setCopyModal={setCopyModal} />
          <KakaoBtn active={true} />
        </BtnBox>
      ) : (
        <BtnBox>
          <CopyBtn active={false} setCopyModal={setCopyModal} />
          <KakaoBtn active={false} />
        </BtnBox>
      )}
    </Container>
  );
};

const PersonalAgree = () => {
  const Container = styled.div`
    padding: 0 4vw 4vw 4vw;
    text-align: left;
    h3 {
      font-weight: 700;
      font-size: 5vw;
    }
    h4 {
      font-size: 4vw;
      font-weight: 700;
      color: #757575;
    }
    p {
      font-size: 3vw;
      font-weight: 400;
      color: #757575;
      line-height: 150%;
    }
  `;
  const TableBox = styled.div`
    height: auto;
    padding: 4vw 0;
    div {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #d9d9d9;
    }
    p {
      display: flex;
      align-items: baseline;
      margin: 0;
    }

    b {
      font-weight: 700;
    }
    h5 {
      margin: 2vw 0;
      font-size: 3vw;
      min-width: 29vw;
      color: #979797;
      font-weight: 700;
    }
    h6 {
      margin: 0;
      font-size: 3vw;
      color: #979797;
      font-weight: 400;
      line-height: 150%;
    }
  `;
  const RejectBox = styled.div`
    height: auto;
    div {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #d9d9d9;
    }
    p {
      display: flex;
      align-items: baseline;
      margin: 0;
    }
    b {
      font-weight: 700;
    }
    h5 {
      margin: 4vw 0;
      font-size: 3vw;
      min-width: 29vw;
      color: #979797;
      font-weight: 700;
    }
    h6 {
      margin: 0;
      font-size: 3vw;
      color: #979797;
      font-weight: 400;
      line-height: 150%;
    }
  `;
  const Detail = styled.div`
    overflow-y: scroll;
    max-height: 60vh;
    padding-bottom: 10vw;
    margin-bottom: 40vw;
  `;
  return (
    <Container>
      <h3>[필수] 개인(신용)정보 수집·이용·제공 동의</h3>
      <h4>이벤트 참여에 따른 개인정보 제3자 제공에 관한 안내</h4>
      <Detail>
        <p>
          주식회사 소셜인베스팅랩은 「신용정보의 이용 및 보호에 관한 법률」,
          「전자금융거래법」 및 「전자금융감독규정」, 「개인정보 보호법」,
          「정보통신망 이용촉진 및 정보보호 등에 관한 법률」 등 관련 법규에 따라
          이용자로부터 아래와 같은 개인정보 제3자 제공 동의를 받고자 합니다.
          내용을 자세히 읽으신 후, 동의하시면 “동의”, “계속” 등 화면의 안내에
          따라 버튼을 눌러 주시기 바랍니다. 보다 자세한 내용은 회사의
          개인정보처리방침을 참조하여 주시기 바랍니다.
        </p>
        <TableBox>
          <div>
            <h5>제공목적</h5>
            <h6>이벤트 상품 지급</h6>
          </div>
          <div>
            <h5>제공목적</h5>
            <h6>(주)티아이커머스</h6>
          </div>
          <div>
            <h5>제공하는 항목</h5>
            <h6>계좌번호, 이름, 휴대폰번호, 주민등록번호</h6>
          </div>
          <p>
            <h5>이용 및 보유 기간</h5>
            <h6>
              서비스의 제공 목적이 달성된 후 파기 (단,
              <b>관계법령에 정해진 규정에 따라 법정기간 동안 보관</b>)
            </h6>
          </p>
        </TableBox>
        <RejectBox>
          <h5>동의를 거부하는 경우에 대한 안내</h5>
          <h6>
            이 동의는 소셜인베스팅랩 이벤트 이용을 위한 필수적인 동의이므로,
            동의를 하지 않으면 해당 서비스를 이용하실 수 없습니다.
          </h6>
        </RejectBox>
      </Detail>
    </Container>
  );
};

const MarketingAgree = () => {
  const Container = styled.div`
    padding: 0 4vw 4vw 4vw;
    text-align: left;
    height: auto;
    h3 {
      font-weight: 700;
      font-size: 5vw;
    }
    h4 {
      font-size: 4vw;
      font-weight: 700;
      color: #757575;
    }
    p {
      font-size: 3vw;
      font-weight: 400;
      color: #757575;
      line-height: 150%;
    }
  `;

  const Detail = styled.div`
    overflow-y: scroll;
    max-height: 60vh;
    padding-bottom: 10vw;
    margin-bottom: 40vw;
  `;

  return (
    <Container>
      <h3>[선택] 마케팅 활용 및 정보 수신 동의</h3>
      <h4>마케팅 활용 및 정보 수신 동의 약관</h4>
      <Detail>
        <p>
          1. 개인정보 수집 및 이용의 목적
          <br /> 신규 기능 개발 및 맞춤 서비스 제공
          <br /> 뉴스레터 발송, 새로운 기능(제품)의 안내
          <br /> 각종 경품, 할인 및 쿠폰 등 이벤트 등의 광고성 정보 제공
          <br /> 영업 및 마케팅 목적
          <br />
        </p>
        <p>
          2. 마케팅 정보 제공
          <br /> 동의를 받아 수집한 귀하의 개인정보는 보유 및 이용기간 중에
          전화, SMS, 카카오톡 메시지, LMS, MMS, 이메일, 푸시 알림, 우편을 통하여
          뉴스레터, 제품정보, 경연대회, 판촉, 설문조사, 사이트 기능 관리,  안부
          문자, 행사 안내 등 영리목적의 광고성 정보 전달에 활용됩니다.
        </p>
        <p>
          3. 수신 동의 및 철회
          <br /> 주식회사 소셜인베스팅랩에서 제공하는 마케팅 정보를 원하지 않을
          경우 고객센터에 문의하여 철회를 요청할 수 있습니다. 또한 향후 마케팅
          활용에 새롭게 동의하고자 하는 경우에도 고객센터에 문의하여 동의하실 수
          있습니다.
          <br />
        </p>
        <p>
          4. 미동의 시 불이익 사항 <br />
          개인정보보호법 제22조 제4항에 의해 선택정보 사항에 대해서는 동의하지
          않으셔도 서비스를 이용하실 수 있습니다. 단, 이벤트 참여가 제한될 수
          있으며 이용자 맞춤형 서비스 등의 마케팅 정보 안내 서비스가 제한됩니다.
          <br />
        </p>
        <p>
          5. 개인정보 수집 및 이용 상세내용
          <br /> 통신사, 이름, 휴대폰번호, 생년월일, 닉네임, 아이디, 이메일,
          이용자가 보유하고 있는 주식의 수량 및 잔고 액수에 관한 정보, 연동 계좌
          정보, 매매 신청 정보 (종목명, 수량, 매매 신청 가격), 수익/손실률, 총
          자산액, 단말기 정보, 디바이스 아이디, 서비스 이용기록 (게시물 작성
          또는 공유, 다른 이용자와의 커뮤니케이션, 게시물 내에 포함된 정보,
          게시물 유형, 이용자가 활성화 및 사용하는 기능, 이용자가 수행하는 행동,
          교류되는 데이터, 이용자의 활동 시간, 빈도 및 기간)
          <br />
        </p>
        <p>
          6. 개인정보 보유 및 이용 기간
          <br /> 서비스 탈퇴 혹은 마케팅 활용 및 정보 수신 동의 철회시까지
        </p>
      </Detail>
    </Container>
  );
};
