import styled from '@emotion/styled';
import React from 'react';
import { CopyToClipboard } from 'react-copy-to-clipboard';
const CopyBtn = ({ active, setCopyModal }) => {
  const Button = styled.button`
    cursor: pointer;
    width: 100%;
    height: 13vw;
    font-size: 4vw;
    font-weight: 700;
    color: #484848;
    text-align: center;
    line-height: 100%;
    border-radius: 2vw;
    background-color: #f5f6fa;
    border: none;
    &:hover {
      background-color: #f5f6fa;
    }
    &:active {
      background-color: #f5f6fa;
    }
  `;

  const DisabledBtn = styled.button`
    width: 100%;
    height: 13vw;
    font-size: 4vw;
    font-weight: 700;
    color: #c8c8c8;
    text-align: center;
    line-height: 100%;
    border-radius: 2vw;
    background-color: #f5f6fa;
    border: none;
  `;

  return (
    <>
      {active ? (
        <CopyToClipboard text={'복사된문구'}>
          <Button onClick={() => setCopyModal(true)}>링크 복사하기</Button>
        </CopyToClipboard>
      ) : (
        <DisabledBtn disabled>링크 복사하기</DisabledBtn>
      )}
    </>
  );
};

export default CopyBtn;
